body {
  background-color: #f6f8fb;
}

.swiper-block {
  position: fixed !important;
  top: 0;
  bottom: 0;
}
.hamMenu {
  background-color: #18489a;
  height: 100%;
  overflow-y: scroll;
}

.mainPnl {
  overflow-y: scroll;
}

.menu-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 25px;
  color: #ffffffa1;
}

.account-info {
  display: flex;
  flex-direction: column;
}

.right-arrow {
  width: 20px;
  height: 20px;
  border-right: 1px solid #ffffffa1;
  border-top: 1px solid #ffffffa1; 
  transform: rotate(45deg);
}

.right-arrow__black {
  border-color: black;
  width: 10px;
  height: 10px;
}

.menu-row {
    padding-bottom: 20px;
    background: #0e2958;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    padding-left: 20px;
    padding-right: 20px;
    color: #fff;
}

.menu-row-title {
  color: #ffffff7a;
  padding: 20px 0;
  font-weight: 600;
  font-size: 14px;
  line-height: 14px;
  border-bottom: 1px solid #ffffff0f;
  margin-bottom: 20px;
}

.menu-row-items {
  display: flex;
  flex-wrap: wrap;
}

.menu-row-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 25%;
  padding-top: 10px;
}

.menu-row-item span {
  text-align: center;
  font-size: 11px;
  font-weight: 600;
  padding: 10px 0;
}

.menu-row-item svg {
  width: 24px;
  height: 24px;
}

.mc-c1 {
  fill: #4471dc;
}

.mc-c2 {
  fill: #f9be18;
}

.st1 {
  fill:none;
}

.st3 {
  fill-rule:evenodd;
  clip-rule:evenodd;
  fill:none
}

.st4 {
  fill-rule:evenodd;
  clip-rule:evenodd;
  fill:none;
}

.main-header {
  padding: 0;
  height: 65px;
  position: relative;
  background: none;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url('./logo.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 120px;
}

/* .main-header img {
  width: 100%;
  max-width: 120px;
} */

.bgEl1 {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 150px;
  background: #18489a;
  z-index: -1;
}

.container {
  padding: 0 15px;
}

.wallet-top-bar, .wallet {
  margin-bottom: 30px;
  border-radius: 5px;
  padding: 15px;
  background-color: #fff;
}

.account-balance {
  margin-bottom: 10px;
  /* display: flex;
  justify-content: space-between; */
  padding: 10px;
  text-justify: newspaper;
}

.account-balance .balance {
  display: inline-block;
  margin-right: 40px;
}

.account-balance .balance span:first-child {
  display: block;
  font-size: 16px;
  font-weight: 600;
}

.account-balance .account-links {
  /* display: flex; */
  display: inline-block;
}

.account-balance .account-links div {
  width: 40px;
  height: 40px;
  border-radius: 5px;
  background-color: #eff2f8;
  display: inline-block;
}

.account-balance .account-links div + div {
  margin-left: 5px;
}

.resent-tr {
  min-width: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #eff2f8;
  padding: 10px 15px;
  border-radius: 8px;
  color: #13171e;
  font-size: 16px;
  font-weight: 600;
}

.wallet-balance {
  padding: 0 10px;
}
